<template>
  <div class="container">
    <Header
      @toggle-add-plan="toggleAddPlan"
      title="Planner"
      :showAddPlan="showAddPlan"
    />
    <div v-show="showAddPlan">
      <AddPlan @add-plan="addPlan" />
    </div>
    <Plans
      @toggle-reminder="toggleReminder"
      @delete-plan="deletePlan"
      :plans="plans"
    />
    <Footer />
  </div>

</template>

<script>
import Header from './components/Header'
import Footer from './components/Footer'
import Plans from './components/Plans'
import AddPlan from './components/AddPlan'

export default {
  name: 'App',
  components: {
    Header,
    Footer,
    Plans,
    AddPlan,
  },
  data() {
    return {
      plans: [],
      showAddPlan: false,
    }
  },
  methods: {
    toggleAddPlan() {
      this.showAddPlan = !this.showAddPlan
    },
    addPlan(plan) {
      this.plans = [ ...this.plans, plan]
    },
    deletePlan(id) {
      if (confirm('Are you sure?')) {
        this.plans = this.plans.filter((plan) => plan.id !== id)
      }
    },
    toggleReminder(id) {
      this.plans = this.plans.map((plan) => plan.id === id ? { ...plan, reminder: !plan.reminder } : plan )
    }
  },
  created() {
    this.plans = [
      {
        "id": "1",
        "text": "Buying apples",
        "day": "November 27th at 11:30am",
        "reminder": true
      },
      {
        "id": "2",
        "text": "Painting oils on canvas",
        "day": "November 27th at 1:30pm",
        "reminder": true
      },
      {
        "id": "3",
        "text": "Working out",
        "day": "November 27th at 8:00pm",
        "reminder": false
      },
      {
        "id": "4",
        "text": "Drawing Illustration",
        "day": "May 24th at 2:30pm",
        "reminder": false
      },
    ]
  },
}
</script>

