<template>
  <footer>
    <p>&copy; 2022. leolego</p>
    <a @click="toggleAbout">About</a>
    <h4 v-show="showVersion">Version 1.0.1</h4>
  </footer>
</template>

<script>
export default {
  name: 'Footer',
  data() {
    return {
      showVersion: false
    }
  },
  methods: {
    toggleAbout() {
      this.showVersion = !this.showVersion;
    }
  }
}
</script>

<style scoped>
  a {
    color: #333;
    text-decoration: underline;
    cursor: pointer;
  }

  footer {
    margin-top: 30px;
    text-align: center;
  }

</style>